
import './styles/main.scss'

const ul = document.querySelector(".todo-wrapper")

const todo_1 = {
    description: "First task",
    complete: false,
    index: 1
}
const todo_2 = {
    description: "Second task",
    complete: false,
    index: 2
}
const todo_3 = {
    description: "Third task",
    complete: false,
    index: 3
}
const todo_4 = {
    description: "Fourth task",
    complete: false,
    index: 4
}

const todoArr = [todo_1, todo_2, todo_3, todo_4];


const stockDom = () => {
    todoArr.forEach( el => {
        let li = document.createElement("li");
        li.classList.add("todo")
        li.innerHTML = `
            <div class="right">
                <input type="checkbox" class="checker"> 
                <span class="text">
                    ${el.description}
                </span>
            </div>
            <div class="dot-wrapper">
                <span class="dot">&#8942;</span>
            </div>
        `;
    
        ul.appendChild(li);
    })
}

window.onload = () => {
    stockDom();
}